
html,body{
    font-family: "Roboto", sans-serif;
    padding: 0;
    margin: 0;
    background-color: var(--theme-page-background);
    color: var(--theme-color);
    transition: background-color 0.50s linear;
    scroll-behavior: smooth;
}

